import React, {useState} from 'react'
import styled from "styled-components";

function Home() {

    const scriptURL = 'https://script.google.com/macros/s/AKfycbxrbw8A_7ep-TUgjgZECnFQThPjhNaXrYvL0AHCwtHXUPdcmxHE/exec'
    
    const form = document.getElementById("preRegister")
    const [disable, setDisable] = useState(false);
    const [email, setEmail] = useState('');
    const [status, setStatus] = useState('');
    
    const handleSubmit = e => {
        e.preventDefault();
        setDisable(true)  
        fetch(scriptURL, { method: 'POST', body: new FormData(form)})
            .then(response => {
                  console.log('Success!', response);
                  setStatus({ type: 'success' });
                  setDisable(false);
                  setEmail('');
                  })
              .catch(error => {
                  setDisable(false);
                  setStatus({ type: 'error', error });
              })  
        }
    
    

    return (
        <home.Wrapper>
            <home.Container>
                <home.Welcome>
                    Welcome to Crypto Scholars!
                </home.Welcome>
                    <h1>Pre-Registration NOW OPEN!</h1>
                    <h3>As a bonus for pre-registering before the cut off date on 12/31/2021 will have early access to the portal in order to set up their profiles
                    </h3>
                    <form name="preRegister" id="preRegister" onSubmit={handleSubmit} >
                        <input className="email_input" type="email" name="email" required placeholder="Enter Email Here" value = {email} onChange={e => setEmail(e.target.value)}/>
                        <button type="submit" id="submit_button" disabled={disable} >Submit</button>
                    </form>
                    <div className="email_result">
                    {status?.type === 'success' && <p>Succes! thank you for Preregistering</p>}
      {status?.type === 'error' && (
          <p>Oh no! something went wrong. Please try again later or contact us on our Discord</p>
          )}
                    </div>
                
            </home.Container>
          </home.Wrapper>
    )
}

const home = {
    
    Wrapper: styled.main`
      display: flex;
      flex-direction: column;
      justify-content: center;
      height:100vh;
      margin: 0 10vw;
    `,

    Container: styled.div`
        text-align:center;
        height: 100%;
        display:flex;
        flex-direction: column;
        justify-content: space-around;
    `,


    Welcome: styled.h1`

    `,
  };

export default Home
